@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;800;900&display=swap");
@import url("coords.css");
@import url("lostPieces.css");
@import url("infoSections.css");
@import url("historySections.css");
@import url("promotion.css");
@import url("gameResult.css");
@import url("boardTestInfo.css");

body {
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100vw;
  font-family: "Montserrat", sans-serif;
}
.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: #302e2b;
}
.game {
  height: 100%;
  aspect-ratio: 1/1.15;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
#root {
  height: 100%;
  aspect-ratio: 1/1.15;
  background-color: #302e2b;
  border-radius: 5px;
  z-index: 1;
  margin: 10px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.App {
  position: relative;
  height: fit-content;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: flex-start;
}
.board {
  display: flex;
  flex-wrap: wrap;
  margin: 15px 0;
}

.cell {
  aspect-ratio: 1 / 1;
  position: relative;
  width: 12.5%;
  height: 12.5%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cell img {
  width: 90%;
  height: 90%;
  position: relative;
  z-index: 1;
}
.white {
  background-color: #e9edcc;
}
.black {
  background-color: #779954;
}
.selected {
  background-color: #bbcc43;
}
.available {
  width: 30%;
  height: 30%;
  opacity: 0.15;
  background-color: black;
  border-radius: 50%;
  position: absolute;
}
.availableToTake {
  width: 100%;
  height: 100%;
  background-color: transparent;
  opacity: 0.15;
  border-radius: 50%;
  position: absolute;
  box-shadow: 0px 0px 0px 10px black inset;
}
@media (max-aspect-ratio: 1/1) {
  .board {
    width: 100%;
    aspect-ratio: 1 / 1;
    display: flex;
    flex-wrap: wrap;
  }
  .App {
    width: 99%;
    aspect-ratio: 1/1.15;
  }
  .game {
    width: 100%;
    aspect-ratio: 1/1.15;
  }
  #root {
    width: 100%;
  }
  .info-section {
    height: 40px;
  }
}
@media (min-aspect-ratio: 1/1) {
  .App {
    height: 98%;
    aspect-ratio: 1/1.4;
  }
  #root {
    height: 100%;
    aspect-ratio: 1/1.4;
  }
  .game {
    height: 100%;
    aspect-ratio: 1/1.4;
  }
  .board {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
  }
}
@media (min-height: 400px) {
  #root,
  .game,
  .app {
    aspect-ratio: 1/1.15;
  }
}
